    bottom: 0;
    left: 0;
    transform: translateX(-100%);
	background: rgb(221,219,221);
	background: -moz-linear-gradient(left,  rgba(221,219,221,1) 0%, rgba(247,247,247,1) 50%, rgba(221,219,221,1) 100%);
	background: -webkit-linear-gradient(left,  rgba(221,219,221,1) 0%,rgba(247,247,247,1) 50%,rgba(221,219,221,1) 100%);
	background: linear-gradient(to right,  rgba(221,219,221,1) 0%,rgba(247,247,247,1) 50%,rgba(221,219,221,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#dddbdd', endColorstr='#dddbdd',GradientType=1 );
    -webkit-animation: shimmer 2s infinite;
	animation: shimmer 2s infinite;
    content: '';
}
@-webkit-keyframes shimmer {
    100% {
      transform: translateX(100%);
    }
}
@keyframes shimmer {
    100% {
      transform: translateX(100%);
    }
}
