.ad_list_loader,
.ad_list_loader:before,
.ad_list_loader:after {
	background: #b0b0b0;
	-webkit-animation: adListLoad 1s infinite ease-in-out;
	animation: adListLoad 1s infinite ease-in-out;
	width: 1em;
	height: 4em;
}
.ad_list_loader {
	color: #b0b0b0;
	text-indent: -9999em;
	margin: 88px auto;
	position: relative;
	font-size: 11px;
	-webkit-transform: translateZ(0);
	-ms-transform: translateZ(0);
	transform: translateZ(0);
	-webkit-animation-delay: -0.16s;
	animation-delay: -0.16s;
}
.ad_list_loader:before,
.ad_list_loader:after {
	position: absolute;
	top: 0;
	content: '';
}
.ad_list_loader:before {
	left: -1.5em;
	-webkit-animation-delay: -0.32s;
	animation-delay: -0.32s;
}
.ad_list_loader:after {
  	left: 1.5em;
}
@-webkit-keyframes adListLoad {
	0%,
	80%,
	100% {
		box-shadow: 0 0;
		height: 4em;
	}
	40% {
		box-shadow: 0 -2em;
		height: 5em;
	}
}
@keyframes adListLoad {
	0%,
	80%,
	100% {
		box-shadow: 0 0;
		height: 4em;
	}
	40% {
		box-shadow: 0 -2em;
		height: 5em;
	}
}
